import React, { useState, useEffect } from 'react';

export const GlobalContext = React.createContext();

export const GlobalStorage = ({ children }) => {
  const [data, setData] = useState([]);
  const [jobs, setJobs] = useState([]);
  const [filter, setFilter] = useState([]);

  useEffect(async () => {
    const response = await fetch("https://danielmafra.github.io/job-listings-with-filtering/api/data.json");
    const json = await response.json();
    setJobs(json);
    setData(json);
  }, []);

  useEffect(() => {
    if (filter != '') {
      setJobs(data);
      jobs.map(job => {
        filter.map(f => {
          setJobs(oldArray => (
            oldArray.filter(value => value.role === f || value.level === f || value.tools.includes(f) || value.languages.includes(f))
          ))
        })
      })
    } else {
      setJobs(data);
    }
  }, [filter]);

  return (
    <GlobalContext.Provider value={{ jobs, filter, setFilter }}>
      {children}
    </GlobalContext.Provider>
  );
};